
.custom-select-button{
    background: #F8F8F8 !important;
    color: #000 !important;
    border: 1px solid #CCCCCC !important;
    text-transform: none !important;
    font-weight: 500 !important;
    padding:0px  10px !important;
    font-size: 11px !important;

    .MuiButton-endIcon{
        margin: 0 !important;
    }
}

.update-button-div{
    margin-top: 20px;
    .update-button{
        background: #04724d !important;
    color: #fff !important;
    border: 1px solid #CCCCCC !important;
    text-transform: none !important;
    font-weight: 500 !important;
    padding:0px  10px !important;
    font-size: 11px !important;
    height: 28px !important;
    }
  
}

.main-filter-div{
    .filter-heading{
        font-size: 8px;
        text-transform: none;
        color: #cccccc;
        margin-bottom: 10px;
    }

    .filter-content{
        padding: 0 10px;
        
    }


}

.main-menu{
    li{
        display: flex;
        justify-content: flex-start;
    }
    li:hover{
        background: none;
    }
}

.range-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 150px;

    .range-div-1,.range-div-3{
     
      
        input{
            width: 45px;
            margin: 0 10px;
            height: 25px;
            border: 1px solid #ccc;
            border-radius: 4px;
            font-size: 10px;
            text-align: center;
        }
    }

    .range-div-2{
        display: flex;
        justify-content: center;
        font-size: 10px;
        color: #333;
        align-items: center;
    }
}


.clear-selection-div{
    display: flex;
    justify-content: space-between;
    align-items: center;


    .clear-all{
        font-size: 12px;
        color: #21745D;
        font-weight: 600;
        cursor: pointer;
    }

    .total-of-total{
        font-size: 10px;
        color: #333;
        opacity: 0.8;
        font-weight: 600;
    }
}
