  border: 1px solid #d8d8d8;
  background-color: #fff;
  padding: 11px 16px 13px 20px;
  margin-top: 10px;
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.resp-project-manager {
  color: #000000;
  font-size: 12px;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
}

.resp-date-time {
  font-size: 10px;
  color: #363636;
  font-family: "Poppins", sans-serif;
}

.resp-comment-text {
  color: #000000;
  font-size: 11px;
  font-family: "Poppins", sans-serif;
  padding-top: 11px;
}

.resp-create {
  width: 22px;
  height: 22px;
  background: #e1e5eb;
  padding: 5px;
  border-radius: 50%;
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.resp-cancel {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 10px;
}

.resp-details-row {
  display: flex;
  padding-top: 4px;
  align-items: center;
}

.resp-update {
  font-size: 8px !important;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  color: #fff;
  background-color: #04724d;
  border-radius: 4px !important;
  width: 60px !important;
  height: 20px !important;
}

.react-rating-select {
  width: 200px !important ;
}

.resp-arrow {
  display: flex;
  align-items: center;
}

.resp-arrow-disabled {
  cursor: not-allowed;
}

.response-select {
  width: 520px;
}

.ShareModal {
  .shareMainDiv {
    width: 60%;
    position: absolute;
    top: 20%;
    left: calc(100% - 50% - 30%);
    .titlee {
      color: #1e1e1e;
      opacity: 1;
      font-weight: bold;
      font-size: 15px;
    }
    .shareLink {
      background: #ffffff 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      // padding: 15px 20px;
      font-family: "Poppins", sans-serif;

      .top {
        display: flex;
        justify-content: space-between;
        padding: 15px 20px 0;

        img {
          width: 24px;
          height: 24px;
        }
      }
      .line {
        border-bottom: #707070 1px solid;
        margin: 10px 0;
      }
      .linkArea {
        padding: 0 20px 15px;
        .LinkTitle {
          color: #000000;
          font-size: 17px;
          font-weight: 600;
        }
        .LinkSubTitle {
          color: #000000;
          font-size: 11px;
          font-weight: 500;
          padding: 10px 0;
        }
      }
    }
    .shareEmail {
      background: #ffffff 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      padding: 15px 20px;
      margin-top: 10px;
      height: 155px;
      .multiselectEmail {
        margin-top: 5px;
        .skillsAuto .MuiInputBase-root {
          padding: 0 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          height: auto !important;
          min-height: 34px !important;
          width: 100% !important;
          margin: 0 !important;
          border: 2px solid #ddd;
          border-radius: 4px;
          background: #fff;
        }

        .skillsAuto .MuiChip-outlined {
          border: 1px solid #333;
          color: #fff;
          background: black;
          height: 20px;
          margin: 5px;
        }
        .MuiChip-outlined .MuiChip-deleteIcon {
          color: #fff;
        }
      }
    }
  }
}

.Linkcopy {
  display: flex;
  justify-content: space-between;
  font-family: "Poppins", sans-serif;
  .linkcopy-input {
    .MuiInputBase-root.Mui-disabled {
      height: 34px;
    }
    .MuiInputBase-root {
      border: none;
    }
  }
  .copy-button {
    background: #fff !important;
    color: #04724d !important;
    border: 1px solid #04724d !important;
    border-radius: 4px !important;
    margin: 0 10px;
    width: 150px !important;
  }
}

.send-response-div {
  padding-top: 20px;
  float: right;

  .send-response-button {
    border-radius: 4px !important;
  }
}

.c-card {
  display: flex;
  flex-direction: row;
  background-color: #e1e5eb;
  min-width: 250px;
  padding: 5px 15px;
  height: 34px;
  font-size: 14px;
}

.updateStatusCard {
  width: 420px;
  width: 420px;
  position: absolute;
  left: calc(100% - 50% - 210px);
  top: calc(100% - 50% - 100px);
  background: #ffffff;
  border-radius: 8px;
  .MuiCardContent-root {
    padding: 0;
  }
  .CardHeader {
    border-bottom: 1px solid #c3c0c0;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    text-align: left;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0px;
    color: #1e1e1e;
    opacity: 1;
    img {
      cursor: pointer;
    }
  }
  .CardBody {
    padding: 20px;
    text-align: left;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
  }
  .buttonArea {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    .Reject {
      background-color: #fff !important;
      color: #04724d !important;
      width: 100px !important;
      height: 35px !important;
      font-weight: bold;
      border: 1px solid #04724d;
      margin-right: 15px !important;
    }
    .Accept {
      width: 100px !important;
      height: 35px;
      font-weight: bold;
    }
  }
}

.score-tooltip-main {
  font-size: 9px;
  width: 150px;

  .main-wrapper-score {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
  }

  .score-heading {
    font-weight: bold;
    width: 33%;
  }

  .score-content {
    width: 33%;
    font-weight: normal;
  }
}

.ToolTipText {
  font-size: 11px;
  font-weight: 600;
}

.isseenFilter {
  width: 150px;
  border-radius: 4px !important;
  [class$="-indicatorSeparator"] {
    background-color: #fff;
  }
}
.resumeIcon {
  margin-right: 4px;
  cursor: pointer;
  background-color: #e6ecf6;
  border-radius: 50%;
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 15px;
  }
}
.CommentModal {
  .MuiCardContent-root {
    padding: 0;
  }
  border-radius: 4px;
  outline: none;
  background-color: white;
  width: 450px;
  height: 500px;
  bottom: 10px;
  right: 10px;
  position: absolute;
  .AreaComment {
    position: relative;
    .headerComment {
      background-color: #d5e5e3;
      padding: 15px 20px;
      display: flex;
      justify-content: space-between;
      text-align: left;
      font-weight: 700;
      font-size: 13px;
      letter-spacing: 0px;
      color: #21745d;
      opacity: 1;
      margin-bottom: 10px;
      img {
        cursor: pointer;
      }
    }
    .NewCommentSection {
      height: 150px;
      bottom: -170px;
      width: calc(100% - 20px);
      margin: 0 10px 10px 10px;
      background-color: #f1f3f6;
      padding: 10px;
      position: absolute;
      border-radius: 10px;
      .commentTextArea {
        border: 1px solid #d8d8d8;
        border-radius: 6px;
        width: 100%;
        outline: none;
      }
      .sendComment {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;
      }
    }
    .OldCommentSection {
      height: calc(450px - 130px - 55px);
      overflow: auto;
      font-family: "Poppins", sans-serif;
      .oldcommentText {
        background: #ffffff;
        border: 1px solid #d8d8d8;
        border-radius: 5px;
        opacity: 1;
        margin: 5px 10px;
        padding: 15px 20px;
        .userName {
          text-align: left;
          font-weight: 700;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
        .timeComment {
          text-align: left;
          font-weight: 400;
          font-size: 9px;
          letter-spacing: 0px;
          color: #363636;
          opacity: 1;
          margin: 2px 0 10px 0;
        }
        .noteComment {
          text-align: left;
          font-weight: 400;
          font-size: 11px;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }
    }
  }
}

.multiple-response-table {
  width: 80%;
  padding-bottom: 50px;
  margin: 50px auto 0 auto;
}
